import React, { useState } from 'react';
import Header from'./Header';
import Footer from './Footer';
import Note from './Note';
//import notes from '../notes';
import ContentArea from './CreateArea';

function App() {
  const [notes , setNotes]=useState([]);

  function addNote(newNote){
    setNotes(prevNotes=>{
      return [...prevNotes,newNote];
    });
  }

  function deletNote(id){
    setNotes(prevNotes=>{
     return prevNotes.filter((notItem,index)=>{
          return index !==id;
      })
    })
  }
  return (
    <div >
     <Header/>
     
      <ContentArea onAdd={addNote}/>

      {notes.map((notItem,index)=>{
        return <Note  key={index} id={index} title={notItem.title} content={notItem.content} onDelete={deletNote}/>
      })}

     {/* {notes.map(noteitem=>{   -->this is for adding information from JSON file or jS Object
       return (<Note key={noteitem.id}
                  title={noteitem.title} 
                  content={noteitem.content}/>)
     })} */}
     <Footer/>
    </div>
  );
}

export default App;
